	margin: 0 auto;
	font-family: 'Droid Sans Mono';

	.cal {
		float: left;
		width: 177px;
	}

	.clndr {
		.clndr-controls {
			display: block;
			display: inline-block;
			width: 100%;
			margin-bottom: 8px;

			&.top {
				margin-bottom: -22px;
				position: relative;
				z-index: 1;
			}

			.clndr-previous-button {
				float: left;
				width: 10%;
				text-align: left;
				cursor: pointer;
				-webkit-user-select: none;

				/* Chrome/Safari */
				-moz-user-select: none;

				/* Firefox */
				-ms-user-select: none;

				/* IE10+ */

				&:hover {
					background-color: #f4f4f4;
				}
			}

			.month {
				text-align: center;
				width: 80%;
				margin: 0 auto;
			}

			.clndr-next-button {
				float: right;
				width: 10%;
				text-align: right;
				cursor: pointer;
				-webkit-user-select: none;

				/* Chrome/Safari */
				-moz-user-select: none;

				/* Firefox */
				-ms-user-select: none;

				/* IE10+ */

				&:hover {
					background-color: #f4f4f4;
				}
			}
		}

		.clndr-grid {
			text-align: center;
			border: 1px solid #FF4545;
			display: inline-block;

			.header-day {
				float: left;
				width: 25px;
				height: 25px;
				background: #FF4545;
			}

			.day {
				float: left;
				width: 25px;
				height: 25px;

				&.event {
					background-color: #B4E09F;
				}

				&.today {
					background-color: #E3C57F;
				}
			}

			.empty,
			.adjacent-month {
				float: left;
				width: 25px;
				height: 25px;
				background: #ddd;
			}
		}

		.clndr-today-button {
			width: 100%;
			text-align: center;
			cursor: pointer;

			&:hover {
				background-color: #ddd;
			}
		}
	}

	.multi-month-controls .quarter-button {
		display: inline-block;
		width: 25%;

		&:hover {
			cursor: pointer;
			background-color: #f4f4f4;
		}
	}
}

.clndr-next-button,
.clndr-previous-button,
.clndr-next-year-button,
.clndr-previous-year-button {
	-webkit-user-select: none;

	/* Chrome/Safari */
	-moz-user-select: none;

	/* Firefox */
	-ms-user-select: none;

	/* IE10+ */
}

.clndr-next-button.inactive,
.clndr-previous-button.inactive,
.clndr-next-year-button.inactive,
.clndr-previous-year-button.inactive {
	opacity: 0.5;
	cursor: default;
}
